.product_header_fixed {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: rem(110);
	z-index: 200;
	background-color: white;

	// display: flex;
	flex-direction: column;
	justify-content: space-between;

	overflow: hidden;

	display: none;

	&__left {
		display: flex;
		flex-wrap: wrap;
		.image {
			display: none;
		}

		.title {
			width: 100%;
			text-align: center;
			display: flex;
			// align-items: center;
			justify-content: center;

			margin:  rem(15) 0;

			span {
				font-size: rem(20);
				line-height: 150%;
				font-family: $font-merri;
			}
		}
	}

	&__right {
		display: flex;
		flex-wrap: wrap;
		// align-items: center;

		ul {
			display: none;
		}

		> a {
			width: 100%;
			background-color: var(--c-accent);
			display: flex;
			align-items: center;
			justify-content: center;

			padding: 0 rem(78);
			
			height: rem(50);

			font-weight: 500;
			font-size: rem(14);
			color: white;
		}
	}
}

.product_header_fixed.active {
	display: flex;
}