.slider_article {
	margin-bottom: rem(64);
	> .container {
		width: 100%;
	}

	h2 {
		text-align: center;
		margin-bottom: rem(16);
	}

	p {
		text-align: center;
		margin: 0 rem(20);
		margin-bottom: rem(48);

		br {
			display: none;
		}
	}

	.swiper-nav {
		margin-top: rem(46);

		button {
			i {
				&::before {
					color: #30384B;
				}
			}
		}
	}

	&__item {

		&:hover {
			h3 {
				color: var(--c-accent);
			}
		}

		img {
			margin-bottom: rem(10);

			width: 100%;
			height: rem(190);
			object-fit: cover;
		}

		span {
			margin-bottom: rem(12);
			font-size: rem(10);
			font-weight: 500;
			text-transform: uppercase;
		}

		h3 {
			letter-spacing: rem(1.2);
		}

		span, h3 {
			margin: 0 rem(20);
		}
	}
}