.page_research {


	.sect_nav {
		border-bottom: 0;
		ul {
			display: flex;
			// justify-content: flex-start;

			li {
				// margin-right: rem(48);
				margin-bottom: 0;

				&:last-child {
					margin-right: 0;
				}

				a {
					text-transform: uppercase;
					font-size: rem(12);

					&:hover {
						color: var(--c-dark);

						border-bottom: rem(2) solid var(--c-accent);
					}
				}
			}

			li.active {
				a {
					color: var(--c-dark);

					border-bottom: rem(2) solid var(--c-accent);
				}
			}
		}
	}

	.articles {
		padding: 0;

		display: flex;
		flex-direction: column;

		.button_primary {
			align-self: center;
			margin-bottom: rem(64);
		}
	}


	&__table {
		.header_links {
			display: flex;
			justify-content: space-between;
			margin-bottom: rem(38);

			a {
				font-size: rem(12);
				font-weight: bold;
				text-transform: uppercase;
				border-bottom: rem(2) solid transparent;

				&:hover {
					border-color: var(--c-accent);
				}
			}

			a.active {
				border-color: var(--c-accent);
			}
		}

		
	}

}

.wrap_links {
	display: flex;
	flex-direction: column;
	border-top: rem(1) solid var(--c-dark);
	margin-bottom: rem(64);

	ul {
		li {
			height: rem(82);
			border-bottom: rem(1) solid var(--c-dark);
			display: flex;
			flex-wrap: wrap;
			align-items: center;
			justify-content: space-between;

			a {
				font-size: rem(14);
				font-weight: 500;
				text-transform: uppercase;
				line-height: 152%;
				letter-spacing: rem(2);

				display: flex;
				justify-content: space-between;
				align-items: center;
				width: 100%;

				&:hover {
					color: var(--c-accent);
				}

				&::after {
					content: "\e90d";
					font-family: 'icons';
					color: var(--c-dark);
					font-size: rem(26);
				}
			}
		}
	}
}