.table_col {
	padding: rem(52) rem(37);
	background-color: var(--c-dirty-blue);

	&__items {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;

		.item {
			width: rem(100);

			display: flex;
			flex-direction: column;
			justify-content: center;

			margin-bottom: rem(32);

			&:nth-child(3), &:last-child {
				margin-bottom: 0;
			}

			.image {
				text-align: center;
				margin-bottom: rem(10);
			}

			p {
				font-weight: 500;
				font-size: rem(12);
				line-height: 130%;
				text-align: center;
				// max-width: rem();

				margin-left: auto;
				margin-right: auto;
			}
		}
	}
}