.page_research_article {

	.w-8\/12,
	.w-4\/12 {
		flex: 0 0 100%;
		max-width: 100%;
	}

	.container {
		width: 100%;

	}

	.w-4\/12 {
		// display: none;
	}

	@media (max-width: 1200px) {
		.wrap_links {
			display: none;
			border-top: 0;

			ul {
				border-bottom: 0;
				margin-bottom: 0;

				li {
					height: auto;
					border-bottom: 0;

					a {
						font-size: rem(14);
						text-transform: none;
						letter-spacing: normal;

						&::after {
							display: none;
						}
					}
				}
			}
		}

		.wrap_links.active {
			display: block;
		}

		.catalog__left {
			overflow-y: hidden;
			// display: block;

			&-nav {
				padding: rem(15) rem(50) rem(70) rem(50);
				margin-bottom: rem(20);
				overflow-y: auto;
				height: 100%;
			}
		}
	}
	

	

	.button_active_aside {
		margin-bottom: rem(32);
		width: 100%;
		padding: rem(20) 0;
		border-top: rem(2) solid var(--c-dark);
		border-bottom: rem(2) solid var(--c-dark);

		display: flex;
		justify-content: center;
		align-items: center;

		margin-top: rem(30);


		span {
			font-size: rem(13);
			font-weight: 500;
			text-transform: uppercase;
			line-height: 152%;
			letter-spacing: rem(2);
			margin-right: rem(14);
		}

		i {
			&::before {
				font-size: rem(32);
			}
		}
	}

	.article {
		margin-left: rem(20);
		margin-right: rem(20);
		margin-top: rem(30);

		h1 {
			font-size: rem(28);
			line-height: 135%;
			font-weight: 500;
			font-family: $font-mplus;
			margin-bottom: rem(24);
		}

		p {
			font-size: rem(14);
			font-family: $font-merri;
			font-weight: 300;
			line-height: 178%;
			margin-bottom: rem(32);
			&:first-of-type {
				font-size: rem(13);
				font-weight: 500;
				line-height: 160%;
				margin-bottom: rem(32);
			}
		}

		h2 {
			font-size: rem(22);
			font-weight: 500;
			font-family: $font-mplus;
			margin-bottom: rem(12);
		}

		&__foot {
			margin-top: rem(32);

			.item {
				margin-bottom: rem(32);

				&:last-child {
					margin-bottom: 0;
				}

				a {
					font-family: $font-merri;
					color: var(--c-accent);
					display: inline-block;
					margin-bottom: rem(8);

					&:hover {
						text-decoration: underline;
					}
				}

				ol {
					li {
						list-style: decimal;
						font-size: rem(13);
						line-height: 160%;
						font-weight: 500;
						margin-left: rem(16);
					}
				}

				p {
					font-size: rem(13);
					line-height: 160%;
					font-weight: 500;
					font-family: $font-mplus;
				}
			}
		}
		
	}
}