.researches {
	position: relative;
	padding: rem(36) 0;

	// background: url(../img/page_goods_about/research_bg_x1.jpg) no-repeat top center;
	// background-size: cover;

	.parallax_overlay {
		position: absolute;
		top: 0;
		left: 0;

		width: 100%;
		height: 100%;

		overflow: hidden;
		
		img {
			width: 100%;
			// height: 100%;
			background-size: cover;
		}
	}

	> .container {
		display: flex;
		flex-direction: column;
	}

	&__left {
		margin-bottom: rem(34);
		h3 {
			font-family: $font-merri;
			font-size: rem(24);
			line-height: 150%;
			margin-bottom: rem(16);
		}

		a {
			font-size: rem(12);
			font-weight: 500;
			text-decoration: underline;

			&:hover {
				text-decoration: none;
			}
		}
	}

	&__right {

		.swiper-container {
			// padding-bottom: rem(65);
		}

		.swiper-nav {
			margin-top: rem(28);

			height: rem(20);

			.swiper-button-disabled {
				i {
					font-size: rem(14);
				}
			}

			button {
				i {
					font-size: rem(20);
					&::before {
						color: #30384B;
					}
				}
			}
		}
	}

	&__item {
		display: flex;
		flex-direction: column;
		background-color: #454D62;
		padding: rem(44) rem(21) rem(33) rem(21);

		height: rem(445);

		h3 {
			font-family: $font-merri;
			font-size: rem(24);
			line-height: 132%;
			color: white;
			margin-bottom: rem(16);

			padding-top: rem(16);
			border-top: rem(1) solid white;
		}

		p, >.flex span:not(.date) {
			font-size: rem(13);
			font-weight: 500;
			line-height: 180%;
			color: white;
			margin-bottom: rem(18);
		}

		>.flex {
			span {
				margin-bottom: 0 !important;
			}
			.date {
				font-family: $font-merri;
				font-weight: 300;
				font-size: rem(24);
				line-height: 132%;
				color: white;
			}
		}

		a {

			align-self: flex-start;

			margin-top: auto;

			font-size: rem(12);
			font-weight: 500;
			line-height: 130%;
			color: white;
			border-bottom: rem(1.5) solid white;
		}
	}
}