#header_faq {
	height: rem(354);
	background: url(../img/page_faq/header_bg_x1.jpg) no-repeat top center;
	background-size: cover;

	.header__bottom {
		margin-top: rem(60);
		
		h1 {
			margin-bottom: rem(20);
			font-weight: normal;
		}

		p {
			font-family: $font-merri;
			font-size: rem(13);
			font-weight: 300;
			max-width: rem(240);
			margin-bottom: rem(30);
		}

		.search_form {
			width: 100%;
			position: relative;
			margin-bottom: rem(20);

			input {
				width: 100%;
				height: rem(42);
				border-radius: rem(25);
				background-color: white;
				padding: 0 rem(20) 0 rem(52);

				color: #A9A9A9;

				@include placeholder {
					color: #A9A9A9;
				}
			}

			button {
				position: absolute;
				top: rem(5);
				left: rem(12);
				width: rem(32);
				height: rem(32);

				display: flex;
				align-items: center;
				justify-content: center;

				i {
					font-size: rem(24);
				}
			}
		}

		a {
			font-weight: 500;
			border-bottom: rem(2) solid var(--c-dark);
		}
	}
}